import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, ColEditDelete, TableData } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import {
  getPracticeClassifications,
  deletePracticeClassifications,
} from 'services/practiceClassification';

class GradedExercisesPage extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deletePracticeClassifications(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const columns = [
      {
        title: 'Xếp loại rèn luyện',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tên tiếng anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'Từ điểm',
        dataIndex: 'fromGrade',
        key: 'fromGrade',
      },
      {
        title: 'Đến điểm',
        dataIndex: 'toGrade',
        key: 'toGrade',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.REN_LUYEN__XEP_LOAI_REN_LUYEN__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Xếp loại rèn luyện">
        <CartHeadAction>
          <Link to={routeConstant.REN_LUYEN__XEP_LOAI_REN_LUYEN__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getPracticeClassifications}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <GradedExercisesPage />
  </Page>
);



